<template>
    <div class="admin-users">
      
      <div class="search-filters">
        <input 
          type="text" 
          v-model="searchQuery"
          placeholder="Search users..."
        >
        <select v-model="roleFilter">
          <option value="">All Roles</option>
          <option value="user">User</option>
          <option value="admin">Admin</option>
        </select>
        <select v-model="statusFilter">
          <option value="">All Status</option>
          <option value="active">Active</option>
          <option value="inactive">Inactive</option>
          <option value="blocked">Blocked</option>
        </select>
      </div>
  
      <table class="users-table">
        <thead>
          <tr>
            <th>ID</th>
            <th>Name</th>
            <th>Email</th>
            <th>Role</th>
            <th>Status</th>
            <th>Joined Date</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="user in filteredUsers" :key="user.id">
            <td>{{ user.id }}</td>
            <td>{{ user.name }}</td>
            <td>{{ user.email }}</td>
            <td>
              <select 
                v-model="user.role"
                @change="updateUserRole(user.id, user.role)"
              >
                <option value="user">User</option>
                <option value="admin">Admin</option>
              </select>
            </td>
            <td>
              <select 
                v-model="user.status"
                @change="updateUserStatus(user.id, user.status)"
              >
                <option value="active">Active</option>
                <option value="inactive">Inactive</option>
                <option value="blocked">Blocked</option>
              </select>
            </td>
            <td>{{ formatDate(user.joinedDate) }}</td>
            <td>
              <button @click="viewUserDetails(user)">View Details</button>
              <button @click="deleteUser(user.id)">Delete</button>
            </td>
          </tr>
        </tbody>
      </table>
  
      <!-- User Details Modal -->
      <div v-if="selectedUser" class="modal">
        <div class="modal-content">
          <h3>User Details</h3>
          
          <div class="user-info">
            <p><strong>Name:</strong> {{ selectedUser.name }}</p>
            <p><strong>Email:</strong> {{ selectedUser.email }}</p>
            <p><strong>Phone:</strong> {{ selectedUser.phone }}</p>
            <p><strong>Address:</strong> {{ selectedUser.address }}</p>
            <p><strong>Role:</strong> {{ selectedUser.role }}</p>
            <p><strong>Status:</strong> {{ selectedUser.status }}</p>
            <p><strong>Joined Date:</strong> {{ formatDate(selectedUser.joinedDate) }}</p>
          </div>
  
          <div class="user-stats">
            <h4>User Statistics</h4>
            <p>Total Orders: {{ selectedUser.totalOrders }}</p>
            <p>Total Spent: ${{ selectedUser?.totalSpent?.toFixed(2) || '0.00' }}</p>
            <p>Last Order: {{ selectedUser.lastOrderDate ? formatDate(selectedUser.lastOrderDate) : 'N/A' }}</p>
          </div>
  
          <button @click="selectedUser = null">Close</button>
        </div>
      </div>
    </div>
  </template>
  
  <script setup>
  import { ref, computed, onMounted } from 'vue'
  import { useStore } from 'vuex'
  
  const store = useStore()
  const searchQuery = ref('')
  const roleFilter = ref('')
  const statusFilter = ref('')
  const selectedUser = ref(null)
  const loading = ref(false)
  
  onMounted(async () => {
    loading.value = true
    try {
      await store.dispatch('user/fetchUsers')
    } finally {
      loading.value = false
    }
  })
  
  const users = computed(() => store.getters['user/getAllUsers'])
  
  const filteredUsers = computed(() => {
    let filtered = [...users.value]
  
    if (searchQuery.value) {
      const query = searchQuery.value.toLowerCase()
      filtered = filtered.filter(user => 
        (user?.name?.toLowerCase() || '').includes(query) ||
        (user?.email?.toLowerCase() || '').includes(query)
      )
    }
  
    if (roleFilter.value) {
      filtered = filtered.filter(user => user?.role === roleFilter.value)
    }
  
    if (statusFilter.value) {
      filtered = filtered.filter(user => user?.status === statusFilter.value)
    }
  
    return filtered
  })
  
  const formatDate = (date) => {
    return new Date(date).toLocaleDateString()
  }
  
  const updateUserRole = async (userId, newRole) => {
    loading.value = true
    try {
      await store.dispatch('user/updateUserRole', {
        userId,
        role: newRole
      })
      await store.dispatch('user/fetchUsers') // Refresh users list
    } finally {
      loading.value = false
    }
  }
  
  const updateUserStatus = async (userId, newStatus) => {
    loading.value = true
    try {
      await store.dispatch('user/updateUserStatus', {
        userId,
        status: newStatus
      })
      await store.dispatch('user/fetchUsers') // Refresh users list
    } finally {
      loading.value = false
    }
  }
  
  const deleteUser = async (userId) => {
    if (confirm('Are you sure you want to delete this user?')) {
        loading.value = true
        try {
            await store.dispatch('user/deleteUser', userId)
            // Show success message
            alert('User deleted successfully')
        } catch (error) {
            // Show error message
            alert('Could not delete user. Please try again.')
            console.log('Delete operation:', error)
        } finally {
            loading.value = false
        }
    }
}
  
  const viewUserDetails = (user) => {
    selectedUser.value = user
  }
  </script>
  
  <style scoped>
  .admin-users {
  padding: var(--spacing-lg);
}

.search-filters {
  display: flex;
  gap: var(--spacing-md);
  margin-bottom: var(--spacing-lg);
}

.search-filters input,
.search-filters select {
  padding: 10px 15px;
  border: 1px solid #e1e1e1;
  border-radius: var(--radius-md);
  background: white;
  min-width: 200px;
}

.users-table {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0;
  background: white;
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-md);
}

.users-table th,
.users-table td {
  padding: 15px;
  text-align: left;
  border-bottom: 1px solid #eee;
}

.users-table th {
  background: var(--primary-color);
  color: black;
  font-weight: 600;
}

.users-table th:first-child {
  border-top-left-radius: var(--radius-md);
}

.users-table th:last-child {
  border-top-right-radius: var(--radius-md);
}

.users-table tr:hover {
  background-color: #f8f9fa;
}

.users-table select {
  padding: 6px 12px;
  border: 1px solid #ddd;
  border-radius: var(--radius-sm);
  background: white;
}

.users-table button {
  padding: 8px 12px;
  margin: 0 5px;
  border: none;
  border-radius: var(--radius-sm);
  cursor: pointer;
  transition: all 0.3s ease;
}

.users-table button:first-child {
  background: #3498db;
  color: white;
}

.users-table button:last-child {
  background: #e74c3c;
  color: white;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal-content {
  background: white;
  padding: var(--spacing-xl);
  border-radius: var(--radius-lg);
  width: 90%;
  max-width: 600px;
  box-shadow: var(--shadow-lg);
}

.modal-content h3 {
  margin-left: 20px;
}

.user-info, 
.user-stats {
  margin-left: 20px;
  padding: var(--spacing-md);
  background: var(--gray-bg);
  border-radius: var(--radius-md);
  margin-bottom: 20px;
}

.user-info p,
.user-stats p {
  margin: var(--spacing-sm) 0;
}

.modal-content button {
  width: 100%;
  padding: 12px;
  background: #3498db;
  color: white;
  border: none;
  border-radius: var(--radius-md);
  cursor: pointer;
  margin-top: var(--spacing-lg);
  transition: all 0.3s ease;
}
  </style>
  